@font-face {
    font-family: 'LapsusPro';
    font-weight: bold;
    src: url(/static/media/LapsusPro-Bold.fd9fbaf2.otf) format("opentype");
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
}

body {
    background-color: #74afd5;
    background-image: url(/static/media/background_clouds.2a49d842.png), url(/static/media/rainbow.a400a47f.png);
    background-repeat: repeat, no-repeat;
    background-size: 500%, 30%;
    background-position: center, bottom -100px right -300px;
}

@media (min-width: 768px) {
    body {
        background-size: 240%, 30%;
    }
}

@media (min-width: 992px) {
    body {
        background-size: 60%, 30%;
    }
}

.navbar {
    background-color: #9b60cd;
    box-shadow: 0 1px 2px rgba(0,0,0,0.4);
}

.navbar-dark .navbar-toggler {
    color: rgba(255, 255, 255, 1);
}

.navbar-dark .navbar-nav .nav-link {
    font-family: 'LapsusPro', cursive;
    padding: 0.5rem 1rem;
    font-size: 24px;
    color: #FFF;
    letter-spacing: 4px;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.7);
    border-top: 1px solid rgba(255,255,255,0.3);
    transition: all 0.2s linear;
}

.navbar-dark .navbar-nav .nav-link:hover{
    -webkit-transform: scale(1.2);
    text-shadow: 0px 0px 50px rgba(255,255,255,0.7), 0px 1px 3px rgba(0,0,0,0.7);
}

.navbar-dark .navbar-nav .nav-link:hover, 
.navbar-dark .navbar-nav .nav-link:focus {
    color: #FFF;
}

.navbar-collapse {
    padding-top: 10px;
}

@media (min-width: 576px) {
    .navbar-dark .navbar-nav .nav-link {
        padding: 0.5rem 1rem;
        font-size: 26px;
    }
}

@media (min-width: 992px) {
    .navbar-collapse {
        padding-top: 0px;
    }
    .navbar-dark .navbar-nav .nav-link {
        padding: 1rem 1rem;
        font-size: 36px;
        border-top: none;
    }
}

@media (min-width: 1200px) {
    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: 2rem;
        padding-left: 2rem;
    }
}

.examples {
    padding-top: 20px;
}

@media (min-width: 992px) {
    .examples {
        padding-top: 90px;
    }
}

@media (min-width: 1200px) {
    .examples {
        padding-top: 180px;
    }
}

.examples .col {
    padding-left: 8px;
    padding-right: 8px;
}

.examples .col img {
    box-shadow: 0 0 2px rgba(0,0,0,1), 0 1px 3px rgba(0,0,0,0.4);
}

.description {
    font-family: 'LapsusPro', cursive;
    font-size: 26px;
    color: #FFF;
    letter-spacing: 4px;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.7);
    padding-bottom: 20px;
}

@media (min-width: 992px) {
    .description {
        padding-bottom: 90px;
    }
}

@media (min-width: 1200px) {
    .description {
        padding-bottom: 180px;
    }
}

.description .btn-primary {
    font-family: 'LapsusPro', cursive;
    font-size: 26px;
    color: #FFF;
    letter-spacing: 4px;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.7);
    background-color: #967ff8;
    border-color: #967ff8;
    padding: 0.5rem 3rem;
    box-shadow:  0 0 2px rgba(0,0,0,1), 0 1px 3px rgba(0,0,0,0.4);
    transition: all 0.2s linear;
}

.description .btn-primary:hover {
    -webkit-transform: scale(1.2);
    text-shadow: 0px 0px 50px rgba(255,255,255,0.7), 0px 1px 3px rgba(0,0,0,0.7);
}

